<template>
  <div>
    <div class="block">
      <el-timeline v-for="item in operation" :key="item.date">
        <el-timeline-item
          :timestamp="item.date"
          placement="top"
          icon="el-icon-star-on"
          color="#67c23a"
        >
          <el-card>
            <h4>{{ item.operation }}</h4>
          </el-card>
        </el-timeline-item>
      </el-timeline>
    </div>
  </div>
</template>

<script>
export default {
  name: "Operationjournal",

  data() {
    return {
      operation: [],
    };
  },

  mounted() {},

  created() {
    this.getOperation();
  },

  methods: {
    getOperation() {
      this.$axios
        .get("/getUserOper", { params: { id: this.$store.getters.getUser.id } })
        .then((response) => {
          // 转换时间格式
          response.data.data.forEach((item) => {
            item.date = this.$moment(item.date).format("YYYY-MM-DD HH:mm:ss");
          });
          this.operation = response.data.data;
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
};
</script>

